.btn {
  background: none;
  width: 40px;
  height: 30px;
  border: 0;
  padding: 0;
  position: fixed;
  top: 20px;
  right: 15px;
  z-index: 20;
}
.main-nav--open .btn .btn__line--2 {
  opacity: 0;
  transition: opacity 0.1s linear 0.3s;
}
.main-nav--open .btn .btn__line--1 {
  top: 50%;
  margin-top: -1px;
  transform: rotate(45deg);
  transition: top 0.3s linear, transform 0.3s linear 0.3s;
}
.main-nav--open .btn .btn__line--3 {
  top: 50%;
  margin-top: -1px;
  transform: rotate(-45deg);
  transition: top 0.3s linear, transform 0.3s linear 0.3s;
}

.btn__line {
  position: absolute;
  height: 2px;
  width: 100%;
  background: #3d2514;
  top: 0;
  left: 0;
}
.btn__line--1 {
  transition: transform 0.3s linear, top 0.3s linear 0.3s;
}
.btn__line--2 {
  top: 50%;
  margin-top: -1px;
  transition: opacity 0.1s linear 0.3s;
}
.btn__line--3 {
  top: 100%;
  margin-top: -2px;
  transition: transform 0.3s linear, top 0.3s linear 0.3s;
}

