  transform: translateY(-16px) rotate(45deg);
}
.catalog__btn--next {
  right: 15px;
}
.catalog__btn--next::after, .catalog__btn--next::before {
  content: "";
  position: absolute;
  width: 3px;
  height: 20px;
  background-color: #3d3d3d;
  right: 13px;
}
.catalog__btn--next::after {
