$mobile: 768px;
$tablet: 1149px;
$desktop: 1329px;

@mixin media($media) {
  @if $media == desktop {
    @media (max-width: $desktop) { @content; }
  }
  @if $media == tablet {
    @media (max-width: $tablet) { @content; }
  }
  @if $media == mobile {
    @media (max-width: $mobile) { @content; }
  }
}

